.btn,
.timestamp,
button {
    font-weight: 500
}

body {
    font-family: Roboto, 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    margin: 0;
    padding: 20px;
    background-color: var(--background-light);
    color: var(--text-primary);
    line-height: 1.6
}

.app-container {
    max-width: 900px;
    margin: 0 auto;
    padding: 20px;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 2px 10px rgba(0, 0, 0, .1)
}

h1 {
    margin-top: 0;
    color: #333;
    text-align: center
}

.input-section,
.search-section {
    display: flex;
    margin-bottom: 20px;
    gap: 10px
}

input[type=text] {
    flex-grow: 1;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 16px
}

button {
    padding: 10px 20px;
    background-color: red;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 16px
}

button:hover {
    background-color: #c00
}

button:disabled {
    background-color: #ccc;
    cursor: not-allowed
}

.error-message {
    color: #e74c3c;
    background-color: #fadbd8;
    padding: 10px;
    border-radius: 4px;
    margin-bottom: 20px
}

.youtube-player {
    margin-bottom: 20px;
    width: 100%;
    display: flex;
    justify-content: center
}

.youtube-player iframe {
    max-width: 100%;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, .1)
}

.transcript-display {
    max-height: 400px;
    overflow-y: auto;
    border: 1px solid #eee;
    border-radius: 4px;
    padding: 15px
}

.transcript-segment {
    margin-bottom: 10px;
    display: flex
}

.timestamp {
    min-width: 60px;
    color: #666;
    margin-right: 10px
}

.transcript-text {
    line-height: 1.5
}

:root {
    --primary-color: #3f51b5;
    --secondary-color: #f50057;
    --text-primary: #212121;
    --text-secondary: #757575;
    --background-light: #f5f5f5;
    --transition-speed: 0.3s
}

.card {
    transition: box-shadow var(--transition-speed) ease;
    border: none !important
}

.card:hover {
    box-shadow: 0 8px 17px 0 rgba(0, 0, 0, .2), 0 6px 20px 0 rgba(0, 0, 0, .19) !important
}

.card-header {
    border-bottom: none
}

.btn {
    text-transform: uppercase;
    letter-spacing: .03em;
    transition: all var(--transition-speed) ease
}

.btn-primary {
    background-color: var(--primary-color);
    border-color: var(--primary-color)
}

.btn-primary:focus,
.btn-primary:hover {
    background-color: #303f9f;
    border-color: #303f9f;
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, .16), 0 2px 10px 0 rgba(0, 0, 0, .12)
}

.btn-outline-secondary:hover {
    background-color: #e0e0e0;
    color: var(--text-primary);
    border-color: #e0e0e0
}

.form-control {
    border-radius: 4px;
    border: 1px solid #ced4da;
    transition: border-color var(--transition-speed) ease-in-out, box-shadow var(--transition-speed) ease-in-out
}

.form-control:focus {
    border-color: var(--primary-color);
    box-shadow: 0 0 0 .2rem rgba(63, 81, 181, .25)
}

.transcript-container {
    max-height: 500px;
    overflow-y: auto;
    border-radius: 4px;
    scrollbar-width: thin;
    scrollbar-color: rgba(0, 0, 0, 0.2) transparent
}

.transcript-container::-webkit-scrollbar {
    width: 6px
}

.transcript-container::-webkit-scrollbar-track {
    background: 0 0
}

.transcript-container::-webkit-scrollbar-thumb {
    background-color: rgba(0, 0, 0, .2);
    border-radius: 20px
}

.btn {
    position: relative;
    overflow: hidden
}

.btn:after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    width: 5px;
    height: 5px;
    background: rgba(255, 255, 255, .5);
    opacity: 0;
    border-radius: 100%;
    transform: scale(1, 1) translate(-50%, -50%)
}

.btn:focus:not(:active)::after {
    animation: 1s ease-out ripple
}

@keyframes ripple {
    0% {
        transform: scale(0, 0);
        opacity: .5
    }

    100% {
        transform: scale(20, 20);
        opacity: 0
    }
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: 400;
    margin-bottom: 1rem
}

.display-5 {
    font-weight: 300 !important
}

@media (max-width:768px) {
    .container {
        padding: .75rem
    }

    .card-body {
        padding: 1rem
    }

    .display-5 {
        font-size: 1.75rem
    }
}
